<template>
  <el-row>
    <nav-bar class="navbar" />

    <div id="article-create">
      <h3>发表文章</h3>

      <form id="image_form">
        <div class="form-elem">
          <span>图片：</span>
          <input v-on:change="onFileChange" type="file" id="file" />
        </div>
      </form>

      <form>
        <!--标题-->
        <div class="form-elem">
          <span>标题：</span>
          <input v-model="title" type="text" placeholder="输入标题" />
        </div>

        <!--分类-->
        <div class="form-elem">
          <span>分类：</span>
          <span v-for="category in categories" :key="category.id">
            <!--样式也可以通过 :style 绑定-->
            <button
              class="category-btn"
              :style="categoryStyle(category)"
              @click.prevent="chooseCategory(category)"
            >
              {{ category.title }}
            </button>
          </span>
        </div>

        <!--标签-->
        <div class="form-elem">
          <span>标签：</span>
          <input
            v-model="tags"
            type="text"
            placeholder="输入标签，用逗号分隔"
          />
        </div>

        <!--正文-->
        <div class="form-elem">
          <span>正文：</span>
          <el-input
            type="textarea"
            :autosize="{ minRows: 4 }"
            v-model="body"
            placeholder="输入正文"
          >
          </el-input>
        </div>

        <div class="form-elem button">
          <button v-on:click.prevent="submit">提交</button>
        </div>
      </form>
    </div>

    <br />
    <br />
    <br />

    <blog-footer />
  </el-row>
</template>

<script>
import NavBar from "@/components/Home/Navbar";
import BlogFooter from "@/components/Home/Blogfooter";
import axios from "axios";
import authorization from "@/utils/authorization";

export default {
  name: "ArticleCreate",
  components: { BlogFooter, NavBar },
  data() {
    return {
      // 文章标题
      title: "",
      // 文章正文
      body: "",
      // 数据库中所有的分类
      categories: [],
      // 选定的分类
      selectedCategory: null,
      // 标签
      tags: "",
      // 标题图 id
      avatarID: null,
    };
  },
  mounted() {
    // 页面初始化时获取所有分类
    axios
      .get("/api/category/")
      .then((response) => (this.categories = response.data));
  },
  methods: {
    // 根据分类是否被选中，按钮的颜色发生变化
    // 这里可以看出 css 也是可以被 vue 绑定的，很方便
    categoryStyle(category) {
      if (
        this.selectedCategory !== null &&
        category.id === this.selectedCategory.id
      ) {
        return {
          backgroundColor: "black",
        };
      }
      return {
        backgroundColor: "lightgrey",
        color: "black",
      };
    },
    // 选取分类的方法
    chooseCategory(category) {
      // 如果点击已选取的分类，则将 selectedCategory 置空
      if (
        this.selectedCategory !== null &&
        this.selectedCategory.id === category.id
      ) {
        this.selectedCategory = null;
      }
      // 如果没选中当前分类，则选中它
      else {
        this.selectedCategory = category;
      }
    },
    onFileChange(e) {
      // 将文件二进制数据添加到提交数据中
      const file = e.target.files[0];
      let formData = new FormData();
      formData.append("content", file);

      axios
        .post("/api/avatar/", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: "Bearer " + localStorage.getItem("access.myblog"),
          },
        })
        .then((response) => (this.avatarID = response.data.id));
    },
    // 点击提交按钮
    submit() {
      const that = this;
      authorization().then((response) => {
        if (response[0]) {
          // 需要传给后端的数据字典
          let data = {
            title: that.title,
            body: that.body,
          };
          // 添加分类
          if (that.selectedCategory) {
            data.category_id = that.selectedCategory.id;
          }
          // 标签预处理
          data.tag = that.tags
            // 用逗号分隔标签
            .split(/[,，]/)
            // 剔除标签首尾空格
            .map((x) => x.trim())
            // 剔除长度为零的无效标签
            .filter((x) => x.charAt(0) !== "");

          // 添加标题图 id
          data.avatar_id = that.avatarID;

          // 将发表文章请求发送至接口
          // 成功后前往详情页面
          const token = localStorage.getItem("access.bishe");
          axios
            .post("/api/article/", data, {
              headers: { Authorization: "Bearer " + token },
            })
            .then(function (response) {
              that.$router.push({
                name: "ArticleDetail",
                params: { id: response.data.id },
              });
            })
            .catch((error) => {
              console.log(error);
            });
        } else {
          alert("令牌过期，请重新登录。");
        }
      });
    },
  },
};
</script>

